.search input {
  font-size: large;
  background: none;
  position:relative;
  height: 20px;
  width: 385px;
  border:none;
  outline:none;
}

.search:hover .lupa {
  filter: invert(11%) sepia(98%) saturate(5510%) hue-rotate(281deg) brightness(100%) contrast(104%);
}

.searchLine.active {
  position: relative;
  top: 80px;
  width: 90%;
  height: 2px;
  background: rgb(153,10,227);
} 

.search {
  padding-top:13px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-top: 2rem;  
}
  
.search img {
  position: relative;
  width: 20px;
  height: 20px;
  margin-right: 5px;
  top:0px;
}

.cross {
  filter: invert(92%) sepia(0%) saturate(912%) hue-rotate(244deg) brightness(79%) contrast(114%);
}

.cross:hover {
  filter: invert(0%) sepia(0%) saturate(3%) hue-rotate(250deg) brightness(101%) contrast(103%);
}
  
@media (max-width: 1423px) {
  .search input {
    width: 145px;
  }
}

@media (max-width: 768px) {
  .search {
    height: 35px;
    display: flex;
    flex-direction: row;
    position:absolute;
    background: white;
    margin-top: 0px;
    top: 0px;
    width: 100%;
    border-bottom: 1px solid lightgrey;
  }
    
  .search input { 
    width: 100%;
  }
    
  .search img {
    width: 20px;
    height: 20px;
    padding-left: 0px;
    margin-left: 22px;
  }
  .searchLine{
    display:none;
  }
}